@font-face {
  font-family: 'RIDIBatang';
  font-weight: 200;
  src: url('/fonts/RIDIBatang.otf') format('opentype');
}

@font-face {
  font-family: 'D2CodingEng';
  font-weight: 200;
  src: url('/fonts/D2Coding-Ver1.3.2-20180524.ttf') format('truetype');
  unicode-range: U+0041-005A, U+0061-007A, U+0030-0039; /* 영문 대소문자 */
}

@font-face {
  font-family: 'D2Coding';
  font-weight: 200;
  src: url('/fonts/D2Coding-Ver1.3.2-20180524.ttf') format('truetype');
}

@font-face {
  font-family: 'D2CodingBold';
  font-weight: 200;
  src: url('/fonts/D2CodingBold-Ver1.3.2-20180524.ttf') format('truetype');
}

@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: D2CodingEng, RIDIBatang, -apple-system, sans-serif;
}
